import './App.css';
import { useState, useEffect, React, lazy, Suspense } from 'react';
import { Button, Container, Nav, Navbar,NavDropdown } from 'react-bootstrap';
import Table from 'react-bootstrap/Table';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
import axios from 'axios';
// import styled from 'styled-components';
import img from './images/bg.png';
import data from './data.js';
const Detail = lazy(() => import ('./routes/Detail.js'));
const Cart = lazy(() => import ('./routes/Cart.js'));
import {useQuery} from "react-query";

// export let Context1 = createContext(); // state 보관함
// https://react-bootstrap.netlify.app/
function App() {

  let [shoes, setShoes] = useState(data);
  let navigate = useNavigate();

  let result = useQuery('작명', ()=>{
    return axios.get('https://codingapple1.github.io/userdata.json')
    .then((a)=>{ 
      return a.data 
    }),
    {staleTime : 2000}
  })

  let [input, setInput] = useState('');
  useEffect(()=>{
    if(isNaN(input)){
      alert('그러지마세요')   
    }
  }, [input])

  let watched
  useEffect(()=>{
    if(localStorage.key(0) !== 'watched'){ 
      localStorage.setItem('watched',JSON.stringify([]))
    }
      watched = localStorage.getItem('watched');
      watched = JSON.parse(watched);
  })

  return (
    <div className="App">
      <Navbar bg="dark" data-bs-theme="dark">
        <Container>
          <Navbar.Brand href="#home">Shoe Shop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{navigate('/blog')}}>홈</Nav.Link>
            <Nav.Link onClick={()=>{navigate('/detail')}}>세부사항</Nav.Link>
            <Nav.Link onClick={()=>{navigate('/about')}}>
              {result.isLoading ? '로딩중' : result.data.name}
            </Nav.Link>
          </Nav>
        </Container>
      </Navbar>

      <div className='main-bg' style={{backgroundImage:'url('+img+')'}}></div>
      <div>
        <Table striped bordered hover>
        <thead>
          <tr>
            <th>최근 본 상품</th>
          </tr>
        </thead>
        <tbody>
          {
            
          }
        </tbody>
        </Table>
      </div>
      <Suspense fallback={<div>로딩중임</div>}>
      <Routes>
        <Route path="/blog" element={
          <>
            <input onChange={(e)=>{setInput(e.target.value)}}/>

            <p>
              <Link to="/" style={{ textDecoration: "none"}}>홈</Link><br/>
              <Link to="/detail" style={{ textDecoration: "none"}}>상세페이지</Link><br/>
              <Link to="/event" style={{ textDecoration: "none"}}>Nested Route 숙제</Link>
            </p>

            <div className='container'>
              <div className='row'>
                {
                  shoes.map(function(a,i){
                    return(
                      <Shoes title={a.title} content={a.content} price={a.price} i={i+1}></Shoes>
                    )
                  })

                }
              </div>
              <div>
                <button onClick={()=>{
                  axios.get('https://codingapple1.github.io/shop/data2.json')
                  .then((data)=>{
                    let copy = [...shoes, ...data.data];
                    setShoes(copy);
                  })
                  .catch(()=>{
                    alert('데이터 불러오기 실패')
                  })

                  Promise.all([])
                }}>더보기</button>
              </div>
            </div>
            
          </>
        } />
        {/* <Route path="/detail/0" element={ <Detail shoes={shoes}/> } />
        <Route path="/detail/1" element={ <Detail shoes={shoes}/> } />
        <Route path="/detail/2" element={ <Detail shoes={shoes}/> } /> */}
        <Route path="/detail/:id" element={ 
          <Detail shoes={shoes}/> 
        } />

        {/* Nested Route 사용법 */}
        <Route path="/event" element={<Event/>}>Nested Route
          <Route path="one" element={ <div>첫 주문시 양배추즙 서비스</div> }></Route>
          <Route path="two" element={ <div>생일기념 쿠폰받기</div> }/>
        </Route>

        {/* Redux 사용법 */}
        <Route path="/cart" element={<Cart/>}></Route>

        {/* 404페이지 등록하는 방법 */}
        <Route path="*" element={<div>없는 페이지</div>} />
      </Routes>
      </Suspense>

      {/* <Context1.Provider value={ {재고, shoes} }>
        <Detail shoes={shoes}/>
      </Context1.Provider> */}
    </div>
  );
}

export default App;

function Shoes(props){
  // let url = 'https://codingapple1.github.io/shop/'+props.link
  return (
    <div className='col-md-4'>
      <img src={'https://codingapple1.github.io/shop/shoes'+props.i+'.jpg'} width="50%"/>
      <h4>상품명 : {props.title}</h4>
      <p>원산지 : {props.content}</p>
      <p>가격 : {props.price}</p>
    </div>
  )
}

function About(){
  return (
    <div>
      <h4>회사정보</h4>
      <Outlet></Outlet>
    </div>
  )
}

function Event(){
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  )
}




